"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import axios from "axios";
import Sidebar from "./components/Sidebar";
import DynamicContent from "./components/DynamicContent";
import AddMenuModal from "./components/AddMenuModal";
import ConfirmModal from "./components/ConfirmModal";
import { LogOut } from "lucide-react";
import "./styles/dashboard.css";

const API_URL = "http://localhost:5000/api";

export default function DashboardPage() {
  const router = useRouter();
  const [authChecked, setAuthChecked] = useState(false);
  const [menuData, setMenuData] = useState([]);
  const [selected, setSelected] = useState(null);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [menuToDelete, setMenuToDelete] = useState(null);

  // üîí Cek login saat halaman pertama kali di-load
  useEffect(() => {
    const verifyUser = async () => {
      try {
        const res = await axios.get(`${API_URL}/auth/check`, {
          withCredentials: true,
        });
        if (!res.data.success) {
          router.push("/login");
        } else {
          setAuthChecked(true);
          fetchMenus();
        }
      } catch (err) {
        console.error("Unauthorized:", err);
        router.push("/login");
      }
    };
    verifyUser();
  }, [router]);

  // üöÄ Ambil data menu
  const fetchMenus = async () => {
    try {
      const res = await axios.get(`${API_URL}/kategori`, {
        withCredentials: true,
      });
      setMenuData(res.data);
    } catch (err) {
      console.error("Gagal mengambil data menu:", err);
    }
  };

  // ‚ûï Tambah menu
  const handleAddMenu = async (newMenu) => {
    try {
      await axios.post(`${API_URL}/kategori`, newMenu, {
        withCredentials: true,
      });
      fetchMenus();
      setShowAddModal(false);
    } catch (err) {
      console.error("Gagal menambah menu:", err);
    }
  };

  // ‚ùå Hapus menu
  const handleDelete = (menu) => {
    setMenuToDelete(menu);
    setShowConfirmModal(true);
  };

  const confirmDelete = async () => {
    try {
      await axios.delete(`${API_URL}/kategori/${menuToDelete.id}`, {
        withCredentials: true,
      });
      fetchMenus();
    } catch (err) {
      console.error("Gagal menghapus menu:", err);
    } finally {
      setShowConfirmModal(false);
      setMenuToDelete(null);
    }
  };

  // üö™ Logout handler
  const handleLogout = async () => {
    try {
      await axios.post(`${API_URL}/auth/logout`, {}, { withCredentials: true });
      router.push("/login");
    } catch (err) {
      console.error("Gagal logout:", err);
    }
  };

  if (!authChecked) {
    return (
      <div className="flex justify-center items-center h-screen text-lg">
        Memeriksa sesi login...
      </div>
    );
  }

  return (
    <div className="flex min-h-screen bg-gradient-to-br from-white to-pink-50">
      {/* Sidebar kiri */}
      <aside className="w-64 border-r bg-white/60 backdrop-blur-md">
        <Sidebar
          menuData={menuData}
          selected={selected}
          setSelected={setSelected}
          onAddMenu={() => setShowAddModal(true)}
          onDelete={handleDelete}
        />
      </aside>

      {/* Konten utama */}
      <main className="flex-1 p-6">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-bold text-gray-800">Dashboard Admin</h1>
          <button
            onClick={handleLogout}
            className="flex items-center gap-2 bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded-lg transition"
          >
            <LogOut size={18} /> Logout
          </button>
        </div>

        <DynamicContent selected={selected} />

        {/* Modal Tambah Menu */}
        {showAddModal && (
          <AddMenuModal
            onClose={() => setShowAddModal(false)}
            onSave={handleAddMenu}
          />
        )}

        {/* Modal Konfirmasi Hapus */}
        {showConfirmModal && (
          <ConfirmModal
            message={`Yakin ingin menghapus "${menuToDelete?.nama}"?`}
            onConfirm={confirmDelete}
            onCancel={() => setShowConfirmModal(false)}
          />
        )}
      </main>
    </div>
  );
}
